module.exports =
  props:
    expandable: String
    type:
      type: String
      default: 'text'
    rows:
      required: false
    id: String
    value:
      required: false
    label: String
    pattern: String
    error: String
    textarea:
      required: false
    floatingLabel:
      required: false
  ready: ->
    componentHandler.upgradeElement @$el


